<template>
  <div id="swiper">
    <!-- swiper -->
    <swiper class="swiper-container" :options="swiperOption">
      <div class="parallax-bg" slot="parallax-bg" data-swiper-parallax="-23%">
        <img v-lazy="swiperVo.image" alt />
      </div>
      <swiper-slide>
        <div class="title" data-swiper-parallax="-100">Slide 1</div>
        <br />
        <div class="subtitle" data-swiper-parallax="-200">Subtitle</div>
        <div class="text" data-swiper-parallax="-300">
          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam dictum mattis velit, sit amet faucibus felis iaculis nec. Nulla laoreet justo vitae porttitor porttitor. Suspendisse in sem justo. Integer laoreet magna nec elit suscipit, ac laoreet nibh euismod. Aliquam hendrerit lorem at elit facilisis rutrum. Ut at ullamcorper velit. Nulla ligula nisi, imperdiet ut lacinia nec, tincidunt ut libero. Aenean feugiat non eros quis feugiat.</p>
        </div>
      </swiper-slide>
      <swiper-slide>
        <div class="title" data-swiper-parallax="-100">Slide 2</div>
        <div class="subtitle" data-swiper-parallax="-200">Subtitle</div>
        <div class="text" data-swiper-parallax="-300">
          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam dictum mattis velit, sit amet faucibus felis iaculis nec. Nulla laoreet justo vitae porttitor porttitor. Suspendisse in sem justo. Integer laoreet magna nec elit suscipit, ac laoreet nibh euismod. Aliquam hendrerit lorem at elit facilisis rutrum. Ut at ullamcorper velit. Nulla ligula nisi, imperdiet ut lacinia nec, tincidunt ut libero. Aenean feugiat non eros quis feugiat.</p>
        </div>
      </swiper-slide>
      <swiper-slide>
        <div class="title" data-swiper-parallax="-100">Slide 3</div>
        <div class="subtitle" data-swiper-parallax="-200">Subtitle</div>
        <div class="text" data-swiper-parallax="-300">
          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam dictum mattis velit, sit amet faucibus felis iaculis nec. Nulla laoreet justo vitae porttitor porttitor. Suspendisse in sem justo. Integer laoreet magna nec elit suscipit, ac laoreet nibh euismod. Aliquam hendrerit lorem at elit facilisis rutrum. Ut at ullamcorper velit. Nulla ligula nisi, imperdiet ut lacinia nec, tincidunt ut libero. Aenean feugiat non eros quis feugiat.</p>
        </div>
      </swiper-slide>
      <div class="swiper-pagination swiper-pagination-white" slot="pagination"></div>
      <!-- <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div> -->
      <!-- <div class="swiper-button-next swiper-button-white" slot="button-next"></div> -->
    </swiper>
  </div>
</template>

<script>
// require styles
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "appCarrousel",
  components: {
    swiper,
    swiperSlide
  },
  props: {
    swiperVo: Object
  },
  data() {
    return {
      swiperOption: {
        // some swiper options/callbacks
        //  swiper  api 
        // ...
        speed: 600,
        parallax: true,
        // loop: true,
        autoplay: {
          delay: 2000,
          disableOnInteraction: false
        },
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        }
      }
    };
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  mounted() {
    // current swiper instance
    // swiper
    // console.log('this is current swiper instance object', this.swiper)
    // this.swiper.slideTo(3, 1000, false)
  }
};
</script>

<style scoped>
#swiper {
  /* position: relative; */
  /* border: 1px dashed red; */
  border-radius: 7px;
  overflow: hidden;
  height: 100%;
}
.swiper-container {
  /* border: 1px dashed green; */
  height: 100%;
}
img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.swiper-slide {
  /* border: 1px dashed yellow; */
  font-size: 18px;
  color: #000;
  /* -webkit-box-sizing: border-box; */
  box-sizing: border-box;
  padding: 40px 60px;
  /* background-color: transparent!important;
  justify-content: space-around!important; */
}
.parallax-bg {
  /* border: 1px dashed green; */
  position: absolute;
  /* left: 0;
  top: 0; */
  width: 130%;
  height: 100%;
  /* background: url('../../assets/images/computer.jpg') no-repeat top center scroll; */
  /* background-size: cover; */
}
.swiper-slide .title {
  font-size: 41px;
  font-weight: 300;
}
.swiper-slide .subtitle {
  font-size: 21px;
}
.swiper-slide .text {
  font-size: 14px;
  max-width: 400px;
  line-height: 1.3;
}
</style>



